@charset "UTF-8";
/* ---------- 마이페이지 ---------- */
@media (max-width: 1200px) {
  .mypage_content {
    flex-direction: column;
    align-items: center;
    height: -moz-fit-content;
    height: fit-content;
  }
  .right {
    width: 100%; /* 오른쪽 영역을 전체 너비로 설정 */
  }
}
.content_mypage {
  padding-top: 80px;
  background-color: #f2f1fe;
  min-width: -moz-fit-content;
  min-width: fit-content;
  height: -moz-fit-content;
  height: fit-content;
  padding-bottom: 300px;
  margin-bottom: -300px;
}
.content_mypage h2 {
  width: 100%;
  text-align: left;
  margin-left: 40px;
  color: #000000;
  opacity: 60%;
  font-size: 26px;
}

.mypage_content {
  display: flex;
  width: 90%;
  height: -moz-fit-content;
  height: fit-content;
  justify-content: center;
  margin: 0 auto;
}
.mypage_content .left {
  margin: 48px 60px 0 0;
  height: -moz-fit-content;
  height: fit-content;
  display: flex;
  flex-direction: column;
  align-items: baseline;
  align-items: center;
}
.mypage_content .left button {
  height: 36px;
  width: 160px;
  border: solid 1px;
  border-radius: 10px;
  color: white;
  font-size: 16px;
  font-weight: 600;
  box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, 0.25);
}
.mypage_content .left .btn_goto_manage {
  background-color: #323232;
  opacity: 80%;
  border-color: #000000;
  margin-right: 60px;
}
.mypage_content .left .btn_goto_manage:hover {
  background-color: #444444;
}
.mypage_content .manage_btn_container {
  display: flex;
  width: 90%;
  justify-content: center;
}
.mypage_content .manage_btn_container button {
  height: 36px;
  width: 160px;
  border: solid 1px;
  border-radius: 10px;
  color: white;
  font-size: 16px;
  font-weight: 600;
  box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, 0.25);
}
.mypage_content .manage_btn_container .levelup_btn {
  background-color: #ffd66b;
  opacity: 80%;
  border-color: #ffd66b;
}
.mypage_content .manage_btn_container .levelup_btn:hover {
  background-color: #ffd66b;
  opacity: 95%;
}
.mypage_content .manage_btn_container .btn_deletemem {
  background-color: #ff2d2d;
  opacity: 80%;
  border-color: #ff2d2d;
  margin-left: 8px;
}
.mypage_content .manage_btn_container .btn_deletemem:hover {
  background-color: #ff2d2d;
  opacity: 95%;
}
.mypage_content .manage_btn_container .btn_editmem {
  background-color: rgba(53, 56, 61, 0.7921568627);
  opacity: 80%;
  border-color: rgba(53, 56, 61, 0.7921568627);
  margin-left: 8px;
}
.mypage_content .manage_btn_container .btn_editmem:hover {
  background-color: rgba(53, 56, 61, 0.7921568627);
  opacity: 95%;
}
.mypage_content .right {
  display: flex;
  flex-direction: column;
}

.mypage_btn {
  width: -moz-fit-content;
  width: fit-content;
  height: -moz-fit-content;
  height: fit-content;
  padding: 2px 6px;
  color: #ffffff;
  background-color: #9747ff;
  opacity: 60%;
  border: solid #9747ff;
  opacity: 60%;
  border-radius: 20px;
  margin: 0 0 6px 8px;
}

.feedNum,
.probNum {
  text-align: right;
  font-size: 20px;
  margin: 0 20px 8px 0;
  color: #897d7d;
  font-weight: bold;
}

.feed_top,
.prob_top {
  margin-top: 20px;
  width: 800px;
  display: flex;
  justify-content: space-between;
}

.noFeed,
.noProblems {
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  font-weight: bold;
  color: hsl(0, 0%, 78%);
}

.memberInfo,
.myProblem,
.myFeedback {
  padding: 10px;
  background-color: #ffffff;
  display: flex;
  flex-direction: column;
  align-items: center;
  font-size: 18px;
  border: solid #c3c2c2 2px;
  border-radius: 10px;
}

.memberInfo {
  height: 50%;
  width: 400px;
  margin: 36px auto 20px;
  justify-content: center;
  padding: 20px;
}
.memberInfo .info_name {
  margin: 20px 0 40px 0;
  display: flex;
  align-items: center;
  flex-direction: column;
  font-weight: bold;
  font-size: 32px;
  color: #060606;
}
.memberInfo .info_name p {
  font-size: 26px;
  color: #000000;
  margin-top: 12px;
}
.memberInfo .text {
  display: flex;
  align-items: center;
  justify-content: center;
}
.memberInfo .text :first-child {
  width: 22%;
  text-align: left;
  margin-right: 12px;
  color: #a7a7a7;
  font-weight: bold;
  font-size: 17px;
}
.memberInfo .text :last-child {
  width: 78%;
  text-align: left;
  font-weight: bold;
  color: hsla(0, 0%, 0%, 0.725);
  font-size: 17px;
}
.memberInfo *:not(:first-child) {
  width: 100%;
  display: flex;
  justify-content: left;
}
.memberInfo *:first-child {
  color: #000000;
  opacity: 80%;
  width: -moz-fit-content;
  width: fit-content;
  display: flex;
  justify-content: left;
}

.myProblem,
.myFeedback {
  height: 240px;
  width: 800px;
  justify-content: center;
}

.myFeedback {
  color: #c3c2c2;
}

.myProblem {
  margin-bottom: 16px;
  display: flex;
  flex-direction: column;
  align-items: start;
  overflow-y: auto; /* 내용 초과시 스크롤바 */
  /* 스크롤바 */
}
.myProblem::-webkit-scrollbar {
  width: 10px;
}
.myProblem::-webkit-scrollbar-thumb {
  background: #c0c0c0; /* 스크롤바 색상 */
  border-radius: 10px; /* 스크롤바 둥근 테두리 */
}
.myProblem::-webkit-scrollbar-track {
  background: rgba(231, 230, 246, 0.5882352941); /*스크롤바 뒷 배경 색상*/
}

.myFeedback {
  margin-bottom: 16px;
  display: flex;
  flex-direction: column;
  align-items: start;
  overflow-y: auto; /* 내용 초과시 스크롤바 */
  /* 스크롤바 */
}
.myFeedback::-webkit-scrollbar {
  width: 10px;
}
.myFeedback::-webkit-scrollbar-thumb {
  background: #c0c0c0; /* 스크롤바 색상 */
  border-radius: 10px; /* 스크롤바 둥근 테두리 */
}
.myFeedback::-webkit-scrollbar-track {
  background: rgba(231, 230, 246, 0.5882352941); /*스크롤바 뒷 배경 색상*/
}

.problems,
.feeds {
  width: 100%;
  display: flex;
  padding: 4px 0;
}
.problems *,
.feeds * {
  display: flex;
  align-items: center;
}

.problemList,
.FeedList {
  width: 100%;
  height: 100%;
  padding: 20px 0;
  margin: 20px 0;
}

.feeds :last-child {
  margin: auto;
  display: flex;
  align-items: center;
}
.feeds .problemId {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 8%;
  color: #04ca00;
  font-weight: bold;
}

.problems .problemId {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 10%;
  color: #04ca00;
  font-weight: bold;
}
.problems .finalScore,
.problems .problemScore {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 7%;
  color: rgba(170, 170, 170, 0.8274509804);
  font-weight: bold;
}
.problems .problemScore {
  margin-right: 10px;
}
.problems .problemTitle,
.problems .mameberName,
.problems .memberGen {
  justify-content: left;
  width: 60%;
  font-weight: bold;
  cursor: pointer;
}

.dash {
  width: 1%;
  color: rgba(31, 31, 31, 0.7019607843);
  color: rgba(170, 170, 170, 0.8274509804);
  font-weight: bold;
}

.feeds .memberGenName {
  justify-content: left;
  width: 14%;
  font-weight: bold;
  color: #262626;
}
.feeds .commentContent {
  margin: 0 20px 0 20px;
  width: 60%;
  color: #1f1f1f;
  cursor: pointer;
}

.problemList :hover,
.FeedList :hover {
  background-color: rgba(243, 242, 242, 0.737);
}

/* noproblems,noFeed 상태일때는 적용x */
.problemList:hover > .noProblems,
.FeedList:hover > .noFeed {
  background-color: transparent;
}

/* 성공여부에 따라 버튼색 변화 */